var sourceType = [
		['camera'], //拍照
		['album'], //相册
		['camera', 'album'] //拍照或相册
	]
	var sizeType = [
		['compressed'], //压缩
		['original'], //原图
		['compressed', 'original'] //压缩或原图
	]
export default{
	data(){
		return{
			displayCate:'',
			data:{},
			goodsid:'',
			uid:'',
			token:'',
			current:1,
			cateCurrent:0,
			 array: ['中国', '美国', '巴西', '日本'],
			imageList: [], //保存图片路径集合
			imageLength: 5, //限制图片张数
			sourceTypeIndex: 2, //添加方式限制
			sizeTypeIndex: 2, //图片尺寸限制
			// 地址
			lat:'',
			lng:'',
			address:'',
			pic:[]
		}
	},
	onLoad(p){
		var that=this
		this.goodsid=p.goodsid
		var userInfo=uni.getStorageSync('userInfo'),that=this
		if(userInfo!='' & userInfo!=null & userInfo!=undefined){
			this.uid=userInfo.uid
			this.token=userInfo.token
		} 
		this.getDetail()
	},
	methods:{
		//选择图片
		chooseImage: async function() {
				uni.chooseImage({
					sourceType: sourceType[this.sourceTypeIndex],

					sizeType: sizeType[this.sizeTypeIndex],

					count: this.imageLength - this.imageList.length,
					success: (res) => {
						this.imageList = this.imageList.concat(res.tempFilePaths);
					}
				})
			},
			//预览图片
			previewImage: function(e) {
				var current = e.target.dataset.src
				uni.previewImage({
					current: current,
					urls: this.imageList
				})
			},
			//删除图片
			deleteImage: function(e) {
				var index = e.target.dataset.index;
				var that = this;
				var images = that.imageList;
				images.splice(index, 1);
				that.imageList = images;
			},
			chooseLocation(){
				var that=this
				uni.chooseLocation({
					success:(res) =>{
						// console.log(res)
						that.lat=res.latitude
						that.lng=res.longitude
						that.address=res.name
					}
				})
			},
			switch1Change(e){
				var v=e.detail.value
				if(v){
					this.data.goodsstatus='3'
				}else{
					this.data.goodsstatus='5'
				}
			},
			radioChange(e){
					var v=e.detail.value
					this.data.ishot=v
			},
			bindPickerChange(e){
				var v=e.detail.value
				this.displayCate=this.array[v].name
				this.data.cates=this.array[v].id
			},
			getDetail(){
				this.$loading()
				var that=this
				var params={
					uid:this.uid,
					token:this.token,
					goodsid:this.goodsid
				}
				var url='&r=api.myshop.goods.edit'
				  this.$apiPost(url,params).then((res) =>{
						that.data=res.data
						that.pic=[]
						that.array=res.data.category
						that.imageList=res.data.thumb.split(',')
						that.displayCate=that.array.filter((item) =>{return item.id==res.data.cates})[0].name
						// that.imageList=res.data
						uni.hideLoading()
				  })
			},
			submit(){
				var  that=this,l=this.imageList.length;
				this.$loading()
				if(l==0){
					this.$msg('至少需上传一张图片')
					return ;
				}else{
					this.imageList.forEach((item,index) =>{
						if(item.indexOf('blob')>-1){
							that.$upload(item,'',(cb) =>{
								that.pic.push(cb)
							var	t=that.pic.length
								if(l==t){
									that.submitForm()
								}
							})
						}else{
							that.pic.push(item)
							var	t=this.pic.length
								if(l==t){
									that.submitForm()
								}
						}
					})
				}
				
			},
			submitForm(){
				var that=this
				var params={
					uid:this.uid,
					token:this.token,
					goodsid:this.goodsid,
					title:this.data.title,
					subtitle:this.data.subtitle,
					unit:this.data.unit,
					thumb:this.pic.join(','),
					marketprice:this.data.marketprice,
					total:this.data.total,
					weight:this.data.weight,
					goodsstatus:this.data.goodsstatus,
					ishot:this.data.ishot,
					displayorder:this.data.displayorder,
					cates:this.data.cates
				}
				var url='&r=api.myshop.goods.doEdit'
				  this.$apiPost(url,params).then((res) =>{
						that.$msg('修改成功')
						uni.hideLoading()
						setTimeout(function(){
							that.getDetail()
						},500)
				  })
			}
	}
}
